<template>
  <div class="release">
    <check-table></check-table>
    <release-form></release-form>
  </div>
</template>

<script type="text/ecmascript-6">
  import CheckTable from '../components/release/check-table.vue';
  import releaseForm from '../components/release/release-form.vue';

  export default {
    components: {CheckTable, releaseForm}
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  .release {
    width: 1200px;
    margin: 0 auto;
  }

  .checkTable {
    margin-bottom: 150px;
  }

  .releaseForm {
  }
</style>



