<template>
  <div class="login">
    <form>
      <input type="text" class="username" placeholder="用户名"
             v-model="formData.username"
             v-validate="'required'" name="用户名">
      <input type="password" class="pwd" placeholder="密码"
             v-model="formData.pwd"
             v-validate="'required'" name="密码">
    </form>
    <div class="routers-box">
      <div class="btn login" @click="login">确定登录</div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Bus from '../utils/bus.js';

  export default {
    data () {
      return {
        formData: {
          username: null,
          pwd: null
        }
      };
    },
    methods: {
      login () {
        this.$validator.validateAll().then((result) => {
          if (result) {
            this.ajaxLogin();  // 表单验证成功后ajax
          } else {
            this.$alert(this.errors.all()[0], '填写错误');  // 验证失败提示
          }
        });
      },
      ajaxLogin () {
        let data = {
          username: this.formData.username,
          pwd: this.formData.pwd
        };
        this.$http.post('superuser/', data).then(res => {
          if (res.body.status === 200) {
            this.$notify.success({message: '登陆成功'});
            Bus.$emit('logined');
            localStorage.setItem('userName', this.formData.username);  // 电话
            this.$router.push('/release');
          }
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import '../assets/css/common.less';

  .login {
    width: 580px;
    margin: 100px auto;
  }

  form {
    .username, .pwd {
      display: block;
      width: 100%;
      height: 64px;
      padding: 0 1em;
      border: 1px solid @cWhite;
      margin-bottom: 20px;
      background-color: @cCyan;
      font-size: 24px;
      color: @cWhite;
    }
  }

  .routers-box {
    margin-top: 20px;
    .btn {
      width: 185px;
      height: 54px;
      cursor: pointer;
      text-align: center;
      line-height: 54px;
      font-size: 24px;
      font-weight: bold;
    }
    .login {
      background-color: @cBorder;
      color: @cDarkCyan;
    }
  }
</style>



