<template>
  <div class="checkTable">
    <el-table :data="dataList" stripe style="width: 100%">
      <el-table-column prop="province" label="省份">
      </el-table-column>
      <el-table-column prop="city" label="市">
      </el-table-column>
      <el-table-column prop="area" label="区（县）">
      </el-table-column>
      <el-table-column prop="address" label="详细地址">
      </el-table-column>
      <el-table-column prop="count" label="任务数量">
      </el-table-column>
    </el-table>
  </div>
</template>

<script type="text/ecmascript-6">
  import Bus from '../../utils/bus';

  export default {
    mounted () {
      this.updateData();  // 初始化数据
      Bus.$on('released', () => {
        this.updateData();
      });
    },
    data () {
      return {
        dataList: [{}]
      };
    },
    methods: {
      updateData () {
        this.$http.get('release_tasks/').then(res => {
          if (res.body.status === 200) {
            let data = res.body.list;
            this.dataList = [{}];  // 清空
            // this.dataList = data;
            data.forEach((val, index) => {
              console.log(val);
              if (!this.dataList[index]) this.dataList.push({});
              let addressList = val.fields.address.split('-');
              this.dataList[index].province = addressList[0];
              this.dataList[index].city = addressList[1];
              this.dataList[index].area = addressList[2];
              this.dataList[index].address = addressList[3];
              this.dataList[index].count = val.fields.total_task_num;
            });
          }
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">

</style>



