<template>
  <div class="merchant">
    <merchant-item v-for="(item, index) in dataList" :key="item.pk"
                   :formData="item">
    </merchant-item>
  </div>
</template>

<script type="text/ecmascript-6">
  import merchantItem from '../components/merchant/merchant-item.vue';

  export default {
    components: {merchantItem},
    mounted () {
      this.initData();  // 初始化数据
    },
    data () {
      return {
        dataList: []
      };
    },
    methods: {
      initData () {
        this.ajaxMerchantData();
      },
      ajaxMerchantData () {
        this.$http.get('merchant_info_show/').then(res => {
          let data = res.body;
          if (data.status === 200) {
            this.dataList = data.list;
          }
        });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">

</style>



