<template>
  <div class="merchantItem">
    <div class="form">
      <div class="left">
        <img class="leftImg" :class="{danger: !formData.fields.pic_file}"
             :src="formData.fields.pic_file">
        <p class="status">状态：{{statusTxt}}</p>
        <div class="btn-line">
          <span class="btns yes" @click="yes">通过</span>
          <i class="bar">|</i>
          <span class="btns no" @click="no">不通过</span>
        </div>
        <input class="remark" type="text" placeholder="拒绝必填原因"
               v-model="remark">
      </div>
      <div class="right">
        <form>
          <inp-line v-model="formData.fields.name" :disabled="true"
                    v-validate.initial="'required'" name="名称"
                    :class="{danger: errors.has('名称')}">
            <div slot="left" class="inp-left">名称</div>
          </inp-line>
          <inp-line v-model="formData.fields.address"
                    :disabled="true"
                    v-validate.initial="'required'" name="地址"
                    :class="{danger: errors.has('地址')}">
            <div slot="left" class="inp-left">地址</div>
          </inp-line>
          <inp-line v-model="formData.fields.mobile" :disabled="true"
                    v-validate.initial="'required'" name="电话"
                    :class="{danger: errors.has('电话')}">
            <div slot="left" class="inp-left">电话</div>
          </inp-line>
          <inp-line v-model="formData.fields.industry" :disabled="true"
                    v-validate.initial="'required'" name="行业"
                    :class="{danger: errors.has('行业')}">
            <div slot="left" class="inp-left">行业</div>
          </inp-line>
          <inp-line v-model="formData.fields.feature" :disabled="true"
                    v-validate.initial="'required'" name="特色"
                    :class="{danger: errors.has('特色')}">
            <div slot="left" class="inp-left">特色</div>
          </inp-line>
        </form>
      </div>
    </div>
  </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import inpLine from '../common/inpLine.vue';

  const REJECT = -1; // 驳回 0 未审核 1 审核通过
  const REVIEWING = 0;
  const PASS = 1;

  export default {
    components: {inpLine},
    props: {
      formData: {
        type: Object,
        default: {}
      },
      remark: ''  // 拒绝必填原因
    },
    computed: {
      pk () {
        if (this.formData.pk) {
          return this.formData.pk;
        }
      },
      statusTxt () {
        if (this.formData.fields.status === REJECT) {
          return '拒绝';
        } else if (this.formData.fields.status === REVIEWING) {
          return '未审核';
        } else if (this.formData.fields.status === PASS) {
          return '审核通过';
        }
      }
    },
    methods: {
      yes () {
        this.ajaxApprpval(1);
      },
      no () {
        this.ajaxApprpval(2);
      },
      ajaxApprpval (res) {
        this.$http.get(`merchant_info_opt/${res}/${this.pk}/${this.remark}/`)
          .then(res => {
            if (res.body.status === 200) {
              this.$notify.info({title: '操作成功'});
            }
          });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  @import '../../assets/css/common.less';

  .merchantItem {

  }

  .form {
    width: 100%;
    margin: 100px 0;
    display: flex;
    justify-content: space-between;
  }

  .left {
    width: 355px;
    img {
      display: inline-block;
      box-sizing: border-box;
      width: 355px;
      height: 355px;
      border: 1px solid @cBorder;
    }
    .danger {
      border: 1px solid red; // 表单验证失败
    }
    .status {
      text-align: center;
      font-size: 20px;
    }
    .btn-line {
      display: flex;
      justify-content: center;
      font-size: 24px;
      color: @cCyan;
      .btns {
        position: relative;
        display: inline-block;
        cursor: pointer;
      }
      .bar {
        display: inline-block;
        margin: 0 10px;
      }
    }
    .remark {
      width: 100%;
      font-size: 20px;
    }
  }

  .right {
    width: 710px;
    form {
      .inp-left {
        display: inline-block;
        width: 4em;
        text-align: center;
        &::after {
          content: '|';
          margin: 0 5px 0 15px;
        }
      }
      input {
        padding: 0;
      }
      .danger {
        border: 1px solid red; // 表单验证失败
      }
    }
  }
</style>



