<template>
  <div class="releaseForm">
    <form>
      <inp-line placeTxt="请输入详细地址"
                v-model="formData.detailAddress"
                v-validate.initial="'required'" name="地址"
                :class="{danger: errors.has('地址')|| formData.seleAddress === null}">
        <div slot="left" class="inp-left-line">
          <span class="inp-left">地址</span>
          <el-cascader :options="addressList"
                       @active-item-change="getMoreAddress"
                       :props="cascaderInit"
                       v-model="formData.seleAddress">
          </el-cascader>
        </div>
      </inp-line>
      <inp-line placeTxt="任务数量（100的整数倍！）"
                v-model="formData.count"
                v-validate="'required'" name="任务数量">
        <span slot="left" class="left">任务数量</span>
      </inp-line>
    </form>
    <button class="btns release" @click="release">发布任务</button>
  </div>
</template>

<script type="text/ecmascript-6">
  import Bus from '../../utils/bus';
  import inpLine from '../common/inpLine.vue';

  export default {
    components: {inpLine},
    mounted () {
      this.ajaxProList();
    },
    data () {
      return {
        formData: {
          seleAddress: null,  // 多级联动地址选择结果
          detailAddress: null,  // 详细地址
          count: null // 任务数量
        },
        addressList: [  // 多级联动地址
          {
            label: '广东',
            children: [
              {
                label: '广州',
                children: [
                  {label: '天河'},
                  {label: '白云'}
                ]
              }
            ]
          }
        ],
        cascaderInit: { // 多级联动设置
          value: 'label'
        }
      };
    },
    methods: {
      release () {
        this.$validator.validateAll().then((result) => {
          if (result) {
            this.ajaxRelease();  // 表单验证成功后ajax
          } else {
            this.$alert(this.errors.all()[0], '填写错误');  // 验证失败提示
          }
        });
      },
      getMoreAddress (val) {
        // val === [省、市、区]
        if (val.length === 1) {
          // 获取市级
          for (let key of Object.keys(this.addressList)) {
            if (val[0] === this.addressList[key].label) {
              this.ajaxCityList(
                this.addressList[key].label,
                (val) => {
                  this.addressList[key].children = val;
                });
              return;
            }
          }
        } else if (val.length === 2) {
          // 获取区级
          for (let key of Object.keys(this.addressList)) {
            if (val[0] === this.addressList[key].label) {
              for (let key2 of Object.keys(this.addressList[key].children)) {
                if (val[1] === this.addressList[key].children[key2].label) {
                  this.ajaxAreaList(
                    this.addressList[key].children[key2].label,
                    (val) => {
                      this.addressList[key].children[key2].children = val;
                    });
                  return;
                }
              }
              return;
            }
          }
        }
      },
      ajaxProList () {
        // 省级列表
        this.$http.get('get_task_province/').then(res => {
          let data = res.body;
          if (data.status === 200) {
            let fixed = [];
            data.province_list.forEach((item, index) => {
              if (!fixed[index]) fixed.push({children: []});
              fixed[index].label = item;
            });
            this.addressList = fixed;
          }
        });
      },
      ajaxCityList (data, reback) {
        // 市列表
        this.$http.post('get_task_city/', {province: data})
          .then(res => {
            let data = res.body;
            if (data.status === 200) {
              let fixed = [];
              data.cities_list.forEach((item, index) => {
                if (!fixed[index]) fixed.push({children: []});
                fixed[index].label = item.fields.num;
              });
              console.log(fixed);
              reback(fixed);
            }
          });
      },
      ajaxAreaList (data, reback) {
        // 市、区列表
        this.$http.post('get_task_area/', {city: data})
          .then(res => {
            let data = res.body;
            if (data.status === 200) {
              let fixed = [];
              data.areas_list.forEach((item, index) => {
                if (!fixed[index]) fixed.push({});
                fixed[index].label = item.fields.areaname;
              });
              console.log(fixed);
              reback(fixed);
            }
          });
      },
      ajaxRelease () {
        let data = {
          province: this.formData.seleAddress[0],
          city: this.formData.seleAddress[1],
          area: this.formData.seleAddress[2],
          detail_address: this.formData.detailAddress,
          amount: this.formData.count
        };
        this.$http.post('release_tasks/', data).then(res => {
          if (res.body.status === 200) {
            this.$notify.success({title: '上传成功'});
            Bus.$emit('released');
          }
        });
      }
    }
  };
</script>

<style rel="stylesheet/less" type="text/less" lang="less">
  .el-cascader__label {
    line-height: 70px;
  }

  .btns {
    font-size: 24px;
  }
</style>



