<template>
  <div class="withdraw">
    <el-table :data="dataList" stripe style="width: 100%">
      <el-table-column label="提现类型">
        <template scope="scope">
          <span v-if="scope.row.fields && scope.row.fields.opt_type === 1">
            积分提现
          </span>
          <span v-else>金币提现</span>
        </template>
      </el-table-column>
      <el-table-column prop="fields.opt_time" label="提现时间">
      </el-table-column>
      <el-table-column prop="fields.user_id" label="用户ID">
      </el-table-column>
      <el-table-column prop="fields.amount" label="提现金额">
      </el-table-column>
      <el-table-column prop="fields.opt_record" label="操作记录">
      </el-table-column>
      <el-table-column label="审批">
        <template scope="scope">
          <el-button @click="yes(scope.row.pk)" type="text">
            通过
          </el-button>
          <el-button @click="no(scope.row.pk)" type="text">
            不通过
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    mounted () {
      this.initData();  // 初始化数据
    },
    data () {
      return {
        dataList: [{}]  // 表格数据
      };
    },
    methods: {
      initData () {
        this.ajaxData();
      },
      yes (pk) {
        this.ajaxChoose(1, pk);
      },
      no (pk) {
        this.ajaxChoose(2, pk);
      },
      ajaxData () {
        this.$http.get('intergral_withdraw_show/').then(res => {
          let data = res.body;
          if (data.status === 200) {
            this.dataList = data.list;
          }
        });
      },
      ajaxChoose (res, pk) {
        // res 1通过 2不通过
        this.$http.get(`intergral_withdraw_opt/${res}/${pk}/`)
          .then(res => {
            if (res.body.status === 200) {
              this.$notify.info({title: '操作成功'});
            }
          });
      }
    }
  };
</script>

<style scoped rel="stylesheet/less" type="text/less" lang="less">
  .withdraw {
    width: 1200px;
    margin: 0 auto;
  }
</style>



